<script>
export default {
  data() {
    return {
      WebTabletSize: 1439,
      WebMobileSize: 767,
      WebWindowSize: {
        width: 0,
        height: 0,
      },
      WebNavExpanded: false,
      HeaderHeight: {
        desktop: 88,
        tablet: 68,
      },
    };
  },
  created() {
    window.addEventListener("resize", this.HandleResize);
    this.HandleResize();
  },
  destroyed() {
    window.removeEventListener("resize", this.HandleResize);
  },
  methods: {
    HandleResize() {
      this.WebWindowSize.width = window.innerWidth;
      this.WebWindowSize.height = window.innerHeight;

      // 調整螢幕大小若大於nav要收起的寬度就收起來
      if (this.WebWindowSize.width > this.WebTabletSize) {
        this.WebNavExpanded = false;
      }
    },
  },
};
</script>