<template>
  <div
    class="eu__b__price"
    id="Freetotry"
    @click="industryCategory.expand = false"
  >
    <div class="eu__b__price__freetotry">
      <div class="eu__b__price__freetotry__info">
        <div class="eu__b__price__freetotry__info__learnmore">詳細了解</div>
        <div class="eu__b__price__freetotry__info__demologo"></div>
        <div class="eu__b__price__freetotry__info__title">
          <span style="font-family: Avenir; font-weight: 600">RE</span>
          <span
            style="font-family: Avenir; font-weight: 600; letter-spacing: 6px"
            >D</span
          >
          <span>編輯器模組</span>
        </div>
      </div>
      <div class="eu__b__price__freetotry__application">
        <div class="eu__b__price__freetotry__application__forms">
          <div class="eu__b__price__freetotry__application__form">
            <div
              class="eu__b__price__freetotry__application__form__user"
              :class="{
                eu__b__price__freetotry__application__form__require:
                  dataPurchaser.purchaser.confirm,
              }"
            >
              <div class="eu__b__price__freetotry__application__form__text">
                <div>姓名*</div>
                <div
                  class="eu__b__price__freetotry__application__form__text__required"
                >
                  請輸入姓名
                </div>
              </div>
              <input
                class="eu__b__price__freetotry__application__form__textinput"
                v-model="dataPurchaser.purchaser.text"
                @keypress.enter="SendConfirm"
              />
            </div>
            <div
              class="eu__b__price__freetotry__application__form__company"
              :class="{
                eu__b__price__freetotry__application__form__require:
                  dataPurchaser.company.confirm,
              }"
            >
              <div class="eu__b__price__freetotry__application__form__text">
                <div>公司名稱*</div>
                <div
                  class="eu__b__price__freetotry__application__form__text__required"
                >
                  請輸入公司名稱
                </div>
              </div>
              <input
                class="eu__b__price__freetotry__application__form__textinput"
                v-model="dataPurchaser.company.text"
                @keypress.enter="SendConfirm"
              />
            </div>
          </div>
          <div class="eu__b__price__freetotry__application__form">
            <div
              class="eu__b__price__freetotry__application__form__industrycategory"
              :class="{
                eu__b__price__freetotry__application__form__require:
                  dataPurchaser.industrycategory.confirm,
              }"
            >
              <div class="eu__b__price__freetotry__application__form__text">
                <div>產業類別*</div>
                <div
                  class="eu__b__price__freetotry__application__form__text__required"
                >
                  請選擇類別
                </div>
                <template v-if="industryCategory.expand">
                  <div
                    class="eu__b__price__freetotry__application__form__industrycategory__items"
                  >
                    <div
                      v-for="(item, index) in industryCategory.items.filter(
                        (i) => i.value > 0
                      )"
                      :key="index"
                      @click.stop="IndustrycategoryDropdownSelect(item)"
                    >
                      {{ item.showtext }}
                    </div>
                  </div>
                </template>
              </div>
              <div
                class="eu__b__price__freetotry__application__form__textinput eu__b__price__freetotry__application__form__textdropdown"
                :class="{
                  eu__b__price__freetotry__application__form__textdropdown_selected:
                    industryCategory.items.filter((i) => i.selected)[0]
                      .value !== 0,
                }"
                @click.stop="IndustrycategoryDropdownClick"
                :data-expand="industryCategory.expand"
              >
                <div>
                  {{
                    industryCategory.items.filter((i) => i.selected)[0].showtext
                  }}
                </div>
              </div>
            </div>
            <div
              class="eu__b__price__freetotry__application__form__phone"
              :class="{
                eu__b__price__freetotry__application__form__require:
                  dataPurchaser.phone.confirm,
              }"
            >
              <div class="eu__b__price__freetotry__application__form__text">
                <div>聯絡電話*</div>
                <div
                  class="eu__b__price__freetotry__application__form__text__required"
                >
                  請輸入電話號碼
                </div>
              </div>
              <input
                class="eu__b__price__freetotry__application__form__textinput"
                v-model="dataPurchaser.phone.text"
                @keypress="isNumber"
                @keypress.enter="SendConfirm"
              />
            </div>
          </div>
          <div class="eu__b__price__freetotry__application__form">
            <div
              class="eu__b__price__freetotry__application__form__email"
              :class="{
                eu__b__price__freetotry__application__form__require:
                  dataPurchaser.email.confirm,
              }"
            >
              <div class="eu__b__price__freetotry__application__form__text">
                <div>Email*</div>
                <div
                  class="eu__b__price__freetotry__application__form__text__required"
                >
                  請輸入電子郵件信箱
                </div>
              </div>
              <input
                class="eu__b__price__freetotry__application__form__textinput"
                type="text"
                v-model="dataPurchaser.email.text"
                @keypress.enter="SendConfirm"
              />
            </div>
          </div>
          <div class="eu__b__price__freetotry__application__form">
            <div class="eu__b__price__freetotry__application__form__remark">
              <div>備註</div>
              <textarea
                class="eu__b__price__freetotry__application__form__textinput"
                v-model="dataPurchaser.remark.text"
              ></textarea>
            </div>
          </div>
        </div>
        <div
          class="eu__b__price__freetotry__application__submit"
          @click="SendConfirm"
        >
          <div class="eu__b__price__freetotry__application__submit__text">
            預約 DEMO
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dataPurchaser: {
        purchaser: { text: "", require: true, confirm: false },
        company: { text: "", require: true, confirm: false },
        industrycategory: { text: "", require: true, confirm: false },
        phone: { text: "", require: true, confirm: false },
        email: { text: "", require: true, confirm: false },
        remark: { text: "", require: false, confirm: false },
      },
      industryCategory: {
        expand: false,
        items: [
          { value: 0, showtext: "請選擇", selected: true },
          { value: 1, showtext: "金融業", selected: false },
          { value: 2, showtext: "保險業", selected: false },
          { value: 3, showtext: "零售業", selected: false },
          { value: 4, showtext: "百貨業", selected: false },
          { value: 5, showtext: "旅遊業", selected: false },
          { value: 6, showtext: "電信業", selected: false },
          { value: 7, showtext: "運輸業", selected: false },
          { value: 8, showtext: "餐飲業", selected: false },
          { value: 9, showtext: "電商業", selected: false },
          { value: 10, showtext: "醫療業", selected: false },
          { value: 11, showtext: "資訊科技", selected: false },
          { value: 12, showtext: "行銷廣告", selected: false },
          { value: 13, showtext: "政府機關", selected: false },
          { value: 14, showtext: "非營利組織", selected: false },
          { value: 15, showtext: "其他", selected: false },
        ],
      },
      timer: null,
      isSend: false,
    };
  },
  props: {
    NotificationOpen: {
      type: Boolean,
      require: true,
    },
  },
  methods: {
    SendConfirm() {
      var self = this;
      var isInvalid = false;

      self.isSend = false;

      const url =
        "https://s1.mailhunter.com.tw/WebDemoReservation/WebDemoReservation.ashx";

      Object.keys(this.dataPurchaser).map(function (objectKey) {
        var objectData = self.dataPurchaser[objectKey];
        // window.console.log(`objectKey: ${objectKey}`);
        // window.console.log(objectData);
        // window.console.log(`objectData.require: ${objectData.require}`);
        // window.console.log(`objectData.text: ${objectData.text}`);

        if (objectData.require && objectData.text === "") {
          objectData.confirm = true;
          isInvalid = true;
        }
        isInvalid = isInvalid || objectData.confirm;
      });

      if (!isInvalid) {
        self.$gtag.event("表單送出", {
          event_category: "預約RED編輯器模組Demo",
          event_label: "MailHunter RED編輯器模組",
          value: "0",
        });

        fetch(url, {
          method: "POST",
          // headers 加入 json 格式
          headers: {
            "Content-Type": "application/json",
          },
          // body 將 json 轉字串送出
          body: JSON.stringify({
            purchaser: self.dataPurchaser.purchaser.text,
            company: self.dataPurchaser.company.text,
            industrycategory: self.dataPurchaser.industrycategory.text,
            phone: self.dataPurchaser.phone.text,
            email: self.dataPurchaser.email.text,
            remark: self.dataPurchaser.remark.text,
            category: "RED編輯器模組",
          }),
        })
          .then((response) => {
            return response.json();
          })
          .then((jsonData) => {
            if (jsonData.Success) {
              // 還原成初始化
              self.dataPurchaser.purchaser.text = "";
              self.dataPurchaser.company.text = "";
              self.dataPurchaser.industrycategory.text = "";
              self.dataPurchaser.phone.text = "";
              self.dataPurchaser.email.text = "";
              self.dataPurchaser.remark.text = "";
              self.industryCategory.items.map(
                (i) => (i.selected = i.value === 0)
              );
              self.isSend = true;

              self.$emit("update:NotificationOpen", true);

              // self.timer = setTimeout(() => {
              //   self.$emit("update:NotificationOpen", false);
              //   clearTimeout(this);
              // }, 1800);
            }
          })
          .catch((err) => {
            window.console.log("錯誤:", err);
          });
      }
    },
    IndustrycategoryDropdownClick() {
      this.industryCategory.expand = !this.industryCategory.expand;
    },
    IndustrycategoryDropdownSelect(item) {
      this.industryCategory.items.map((i) => (i.selected = false));
      item.selected = true;
      // 下拉縮起
      this.industryCategory.expand = false;
      this.dataPurchaser.industrycategory.text = item.showtext;
    },
    validEmail: function (email) {
      var re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(email);
    },
    isNumber(evt) {
      // 只能輸入0~9，+
      if ((evt.keyCode !== 43 && evt.keyCode < 48) || evt.keyCode > 57)
        evt.preventDefault();
    },
  },
  watch: {
    "dataPurchaser.purchaser.text": function (wtext) {
      var self = this;
      if (wtext === "" && !self.isSend) {
        self.dataPurchaser.purchaser.confirm = true;
      } else {
        self.dataPurchaser.purchaser.confirm = false;
      }
    },
    "dataPurchaser.company.text": function (wtext) {
      var self = this;
      if (wtext === "" && !self.isSend) {
        self.dataPurchaser.company.confirm = true;
      } else {
        self.dataPurchaser.company.confirm = false;
      }
    },
    "dataPurchaser.industrycategory.text": function (wtext) {
      var self = this;
      if (wtext === "" && !self.isSend) {
        self.dataPurchaser.industrycategory.confirm = true;
      } else {
        self.dataPurchaser.industrycategory.confirm = false;
      }
    },
    "dataPurchaser.phone.text": function (wtext) {
      var self = this;
      if (wtext === "" && !self.isSend) {
        self.dataPurchaser.phone.confirm = true;
      } else {
        self.dataPurchaser.phone.confirm = false;
      }
    },
    "dataPurchaser.email.text": function (wtext) {
      var self = this;
      if (wtext === "" && !self.isSend) {
        self.dataPurchaser.email.confirm = true;
      } else {
        if (self.validEmail(wtext) || self.isSend) {
          self.dataPurchaser.email.confirm = false;
        } else {
          self.dataPurchaser.email.confirm = true;
        }
      }
    },
  },
};
</script>