      <div class="eu__b__cover__text__bannerlogo"></div>
      <div class="eu__b__cover__text__title">
        <span style="font-family: Avenir; font-weight: 600;">RE</span>
        <span style="font-family: Avenir; font-weight: 600; letter-spacing: 6px;">D</span>
