<script>
export default {
  methods: {
    HeaderItem(id) {
      var target = document.getElementById(id);
      var ht = this.WebWindowSize.width > this.WebTabletSize ? 87 : 67;

      if (window.scrollTo) {
        window.scrollTo({ behavior: "smooth", top: target.offsetTop - ht });
      }
    },
  },
};
</script>