function SetItem(carouselData) {
    var itemFunction = function () {
        // 目前輪播項目
        var nowCarousel = carouselData.item.filter(c => c.selected === true)[0];
        var nowIndex = carouselData.item.indexOf(nowCarousel);

        //  下一個輪播項目
        var nextIndex = nowIndex + 1 == carouselData.item.length ? 0 : nowIndex + 1;

        // 設定fade in out
        SetTimeoutShow(carouselData, carouselData.item[nextIndex]);
    }
    return itemFunction;
}

function TimerSet(fn, t) {
    var timerObj = setInterval(fn, t);

    this.stop = function () {
        if (timerObj) {
            clearInterval(timerObj);
            timerObj = null;
        }
        return this;
    }

    // start timer using current settings (if it's not already running)
    this.start = function () {
        if (!timerObj) {
            this.stop();
            timerObj = setInterval(fn, t);
        }
        return this;
    }

    // start with new or original interval, stop current interval
    this.reset = function (newT = t) {
        t = newT;
        return this.stop().start();
    }
}

function SetTimeoutShow(carouselData, thisItem) {
    if (!thisItem.selected) {
        carouselData.show = false;

        window.setTimeout(function () {
            // 把所有區塊的selected改為false
            carouselData.item.map(cItem => (cItem.selected = false));
            thisItem.selected = true;
            carouselData.show = true;
        }, 200);
    }
}

export default {
    SetItem,
    TimerSet,
    SetTimeoutShow
}