<template>
  <div>
    <Header />
    <div>
      <transition name="eu__animation__fade">
        <notification v-if="NotificationOpen" @close="NotificationOpen = false">
        </notification>
      </transition>
      <Cover />
      <Introduction />
      <Price :NotificationOpen.sync="NotificationOpen" />
      <Footer />
    </div>
  </div>
</template>

<script>
import Header from "@/components/Header.vue";
import Cover from "@/components/Cover.vue";
import Introduction from "@/components/Introduction.vue";
import Price from "@/components/Price";
import Footer from "@/components/Footer";
import notification from "@/components/notification/notification";

export default {
  data() {
    return {
      NotificationOpen: false,
    };
  },
  components: {
    Header,
    Cover,
    Introduction,
    Price,
    Footer,
    notification,
  },
};
</script>