<template>
  <header class="eu__b__header">
    <div class="eu__b__header__container">
      <div class="eu__b__header__container__logo"></div>
      <nav class="eu__b__header__container__nav">
        <div class="eu__b__header__container__nav__item" @click="HeaderItem('Freetotry')">
          <span>預約 Demo</span>
          <div class="eu__b__header__container__nav__item__underline"></div>
        </div>
      </nav>
    </div>
  </header>
</template>

<script>
export default {
  methods: {
    HeaderItem(id) {
      var target = document.getElementById(id);
      var ht = this.WebWindowSize.width > this.WebTabletSize ? 87 : 67;
      
      if (window.scrollTo) {
        window.scrollTo({ behavior: "smooth", top: target.offsetTop - ht });
      }
    },
  },
};
</script>